[col], [cols], [row], [rows], [jc], [ac], [ai], [as], [wrap] {
  display: flex; }

[col], [rows] {
  flex-direction: column; }

[row], [cols] {
  flex-direction: row; }

[jc='c'] {
  justify-content: center; }

[jc='fe'] {
  justify-content: flex-end; }

[jc='sb'] {
  justify-content: space-between; }

[jc='sa'] {
  justify-content: space-around; }

[ac='c'] {
  align-content: center; }

[ac='sb'] {
  align-content: space-between; }

[ac='sa'] {
  align-content: space-around; }

[ac='fs'] {
  align-content: flex-start; }

[ac='fe'] {
  align-content: flex-end; }

[ai='c'] {
  align-items: center; }

[as='c'] {
  align-self: center; }

[as='fs'] {
  align-self: flex-start; }

[as='fe'] {
  align-self: flex-end; }

[wrap] {
  flex-wrap: wrap; }

#loader {
  background-color: white;
  overflow-y: hidden;
  overflow-x: hidden;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 99999999; }
  #loader .lds-ellipsis {
    z-index: 99999999;
    display: inline-block;
    position: absolute;
    width: 80px;
    height: 80px;
    left: 50%;
    top: 25%;
    transform: translateX(-50%); }
    #loader .lds-ellipsis div {
      position: absolute;
      top: 2rem;
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      background-color: #242424;
      animation-timing-function: cubic-bezier(0, 1, 1, 0); }
      #loader .lds-ellipsis div:nth-child(1) {
        left: .5rem;
        animation: lds-ellipsis1 .8s infinite; }
      #loader .lds-ellipsis div:nth-child(2) {
        left: .5rem;
        animation: lds-ellipsis2 .8s infinite; }
      #loader .lds-ellipsis div:nth-child(3) {
        left: 2rem;
        animation: lds-ellipsis2 .8s infinite; }
      #loader .lds-ellipsis div:nth-child(4) {
        left: 3.5rem;
        animation: lds-ellipsis3 .8s infinite; }

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(0); } }

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0); }
  100% {
    transform: translate(1.5rem, 0); } }

* {
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
  font-size: 16px;
  font-weight: 400; }

html {
  min-height: 100vh; }
  html body, html #wrapper {
    min-height: inherit; }
  html body {
    margin: 0; }
    html body noscript {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); }
    html body ul {
      margin: 0;
      padding: 0; }
      html body ul li {
        list-style: none;
        text-transform: capitalize; }
    html body .button,
    html body nav, html body .nav {
      user-select: none; }
    html body .main > *:not(:nth-last-child(1)), html body .main .main-container > *:not(:nth-last-child(1)) {
      margin-bottom: 2rem; }
    html body .main .main-container {
      width: 100%; }
    html body p {
      padding: 0;
      margin: 0;
      font-weight: 300; }

