.calc {
  background-color: white;
  width: 16rem;
  position: relative;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.333); }
  .calc *:not(input) {
    user-select: none; }
  .calc input {
    border: none;
    outline: none;
    width: 100%;
    padding: 2.5rem 1rem;
    box-sizing: border-box;
    font-size: 20px;
    font-family: 'MD Mono', monospace;
    text-align: center; }
  .calc .calc-container .calc-buttons-controls span:hover {
    background-color: rgba(36, 36, 36, 0.2); }
  .calc .calc-container .calc-buttons-controls span:active {
    background-color: rgba(36, 36, 36, 0.4); }
  .calc .calc-container * span {
    cursor: pointer;
    width: 4rem;
    height: 4rem;
    text-align: center;
    line-height: 4rem;
    color: white;
    font-weight: 500;
    font-size: 18px;
    transition: 75ms cubic-bezier(0.645, 0.045, 0.355, 1); }
    .calc .calc-container * span:hover {
      background-color: rgba(255, 255, 255, 0.1); }
    .calc .calc-container * span:active {
      background-color: rgba(255, 255, 255, 0.2); }
  .calc .calc-container [class^=calc-buttons-] {
    width: 16rem;
    flex-wrap: wrap; }
  .calc .calc-container .calc-buttons-operators-advanced {
    background-color: #0d3b47; }
  .calc .calc-container .calc-buttons-operators-basic {
    background-color: #124f5e; }
  .calc .calc-container .calc-container-numbers-controls .calc-buttons-numbers {
    width: 12rem;
    background-color: #242424;
    flex-wrap: wrap-reverse; }
  .calc .calc-container .calc-container-numbers-controls .calc-buttons-controls {
    width: 4rem;
    background-color: #237956; }

html body #wrapper span.mdi {
  box-sizing: content-box;
  font-size: 1.5rem; }
  html body #wrapper span.mdi.icon-rotate-180 {
    transform: rotateZ(180deg); }
  html body #wrapper span.mdi.icon-clickable {
    cursor: pointer;
    user-select: none;
    padding: .5rem;
    border-radius: 50%; }
    html body #wrapper span.mdi.icon-clickable:hover {
      background-color: rgba(36, 36, 36, 0.1); }
    html body #wrapper span.mdi.icon-clickable:active {
      background-color: rgba(36, 36, 36, 0.2); }
  html body #wrapper span.mdi.icon-size-1-1 {
    font-size: 1.125rem; }
  html body #wrapper span.mdi.icon-size-1-5 {
    font-size: 1.5rem; }
  html body #wrapper span.mdi.icon-size-2 {
    font-size: 2rem; }
  html body #wrapper span.mdi.icon-size-2-3 {
    font-size: 2.25rem; }
  html body #wrapper span.mdi.icon-size-3 {
    font-size: 3rem; }

html body #wrapper {
  overflow-y: hidden;
  background-color: #e6e6e6; }
  html body #wrapper .main {
    height: 100vh;
    width: 100%;
    padding: 4rem; }
