@function getColor($hsl, $level) {
  @return hsl( nth($hsl, 1), nth($hsl, 2), nth($hsl, 3) + 5.5 * $level ); }

@function hsl_map($hsl_map) {
  $h: nth($hsl_map, 1);
  $s: nth($hsl_map, 2);
  $l: nth($hsl_map, 3);
  @return hsl($h, $s, $l); }
@mixin transition($easing: quart, $speed: fast) {
  @if $easing == quart {
    $easing: $quart; }

  @else if $easing == ease {
    $easing: $ease; }

  @if $speed == fast {
    $speed: 75; }

  @else if $speed == normal {
    $speed: 150; }

  @else if $speed == slow {
    $speed: 250; }

  transition: #{$speed}ms $easing; }

@mixin shadow($x, $y, $blur, $color) {
  box-shadow: $x $y $blur $color; }

@mixin shadow-def {
  @include shadow( 0, 0, 2rem, rgba(0,0,0,.333) ); }

@mixin shadow-0 {
  @include shadow( 0, 2rem, 4rem, rgba(0,0,0,.333) ); }

@mixin shadow-1 {
  @include shadow( 0, 1rem, 2rem, rgba(0,0,0,.333) ); }

@mixin shadow-2 {
  @include shadow( 0, .5rem, 1rem, rgba(0,0,0,.333) ); }

@mixin shadow-3 {
  @include shadow( 0, .25rem, .5rem, rgba(0,0,0,.333) ); }

@mixin clickable($property, $color, $step, $mode) {
  cursor: pointer;
  #{$property}: $color;

  @if $mode == darken {
    &:hover {
      #{$property}: darken($color, $step); }
    &:active {
      #{$property}: darken($color, $step*2); } }

  @else if $mode == lighten {
    &:hover {
      #{$property}: lighten($color, $step); }
    &:active {
      #{$property}: lighten($color, $step*2); } } }
// Font
$weight-light: 300;
$weight-regular: 400;
$weight-medium: 500;
$weight-bold: 700;

$headings: ('font-size': 1.5rem), ('font-size': 1.25rem, 'font-weight': $weight-medium), ('font-size': 1rem), ('font-size': 0.875rem, 'font-weight': $weight-medium), ('font-size': 0.875rem), ('font-size': 0.75rem);

// Colors
$s-r: (0, 90, 52);
$s-o: (32, 88, 49);
$s-y: (57, 100, 46);
$s-g: (119, 80, 46);
$s-c: (156, 80, 47);
$s-a: (192, 93, 44);
$s-b: (215, 82, 46);
$s-v: (266, 79, 49);
$s-p: (306, 88, 46);

$n-d-1: (0, 0, 10);
$n-d: (0, 0, 14);
$n-l: (0, 0, 90);
$n-l1: (0, 0, 95);

$colors: ($s-r, 's-r'), ($s-o, 's-o'), ($s-y, 's-y'), ($s-g, 's-g'), ($s-c, 's-c'), ($s-a, 's-a'), ($s-b, 's-b'), ($s-v, 's-v'), ($s-p, 's-p'), ($n-d-1, 'n-d-1'), ($n-d, 'n-d'), ($n-l, 'n-l'), ($n-l1, 'n-l1');

// Timing functions
$ease: cubic-bezier(0.645, 0.045, 0.355, 1);
$quart-in: cubic-bezier(0.895, 0.03, 0.685, 0.22);
$quart-out: cubic-bezier(0.165, 0.84, 0.44, 1);
$quart: cubic-bezier(0.76, 0, 0.24, 1);
@each $color in $colors {

  @for $level from -3 through 3 {

    .color-#{ nth($color, 2) }#{ $level } {
      color: getColor( nth($color, 1), $level ); }

    .bg-#{ nth($color, 2) }#{ $level } {
      background-color: getColor( nth($color, 1), $level ); } } }
[col], [cols], [row], [rows], [jc], [ac], [ai], [as], [wrap] {
  display: flex; }

[col], [rows] {
  flex-direction: column; }
[row], [cols] {
  flex-direction: row; }

[jc='c'] {
  justify-content: center; }
[jc='fe'] {
  justify-content: flex-end; }
[jc='sb'] {
  justify-content: space-between; }
[jc='sa'] {
  justify-content: space-around; }

[ac='c'] {
  align-content: center; }
[ac='sb'] {
  align-content: space-between; }
[ac='sa'] {
  align-content: space-around; }
[ac='fs'] {
  align-content: flex-start; }
[ac='fe'] {
  align-content: flex-end; }

[ai='c'] {
  align-items: center; }
[as='c'] {
  align-self: center; }
[as='fs'] {
  align-self: flex-start; }
[as='fe'] {
  align-self: flex-end; }

[wrap] {
