#loader {
  background-color: white;
  overflow-y: hidden;
  overflow-x: hidden;
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 99999999;

  .lds-ellipsis {
    z-index: 99999999;
    display: inline-block;
    position: absolute;
    width: 80px;
    height: 80px;
    left: 50%;
    top: 25%;
    transform: translateX(-50%);

    div {
      position: absolute;
      top: 2rem;
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      background-color: getColor($n-d, 0);
      animation-timing-function: cubic-bezier(0, 1, 1, 0);

      &:nth-child(1) {
        left: .5rem;
        animation: lds-ellipsis1 .8s infinite; }

      &:nth-child(2) {
        left: .5rem;
        animation: lds-ellipsis2 .8s infinite; }

      &:nth-child(3) {
        left: 2rem;
        animation: lds-ellipsis2 .8s infinite; }

      &:nth-child(4) {
        left: 3.5rem;
        animation: lds-ellipsis3 .8s infinite; } } }

  @keyframes lds-ellipsis1 {
    0% {
      transform: scale(0); }

    100% {
      transform: scale(1); } }

  @keyframes lds-ellipsis3 {
    0% {
      transform: scale(1); }

    100% {
      transform: scale(0); } }

  @keyframes lds-ellipsis2 {
    0% {
      transform: translate(0, 0); }

    100% {
      transform: translate(1.5rem, 0); } } }
* {
  box-sizing: border-box;
  font: {
    family: "Ubuntu", sans-serif;
    size: 16px;
    weight: $weight-regular; } }

html {
  min-height: 100vh;

  body, #wrapper {
    min-height: inherit; }

  body {
    margin: 0;

    noscript {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%); }

    ul {
      margin: 0;
      padding: 0;

      li {
        list-style: none;
        text-transform: capitalize; } }

    .button,
    nav, .nav {
      user-select: none; }

    .main {
      &, .main-container {
        & > *:not(:nth-last-child(1)) {
          margin-bottom: 2rem; } }

      .main-container {
        width: 100%; } }

    p {
      padding: 0;
      margin: 0;
      font-weight: $weight-light; } } }
@each $value in $headings {
  $i: index($headings, $value);

  h#{$i}, .h#{$i} {
    margin: 0;
    padding: 0;
    font-size: map-get($value, 'font-size');
    line-height: map-get($value, 'font-size') + .25rem;

    @if $i == 1 {
      letter-spacing: 0.0625rem; }

    @if $i == 6 {
      opacity: 0.75; }

    @if map-get($value, 'font-weight') != null {
      font-weight: map-get($value, 'font-weight'); }

    @else {
      font-weight: $weight-regular; } } }

sub, sup {
  font-size: .8rem; }
.calc {
  background-color: white;
  width: 16rem;
  position: relative;
  @include shadow-1;

  *:not(input) {
    user-select: none; }

  input {
    border: none;
    outline: none;
    width: 100%;
    padding: 2.5rem 1rem;
    box-sizing: border-box;
    @import url('https://fonts.googleapis.com/css2?family=DM+Mono&display=swap');
    font-size: 20px;
    font-family: 'MD Mono', monospace;
    text-align: center; }

  .calc-container {
    .calc-buttons-controls {
      span {
        &:hover {
          background-color: rgba(hsl-map($n-d),.2); }

        &:active {
          background-color: rgba(hsl-map($n-d),.4); } } }

    *:not(.calc-buttons-controls > span) {
      span {
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        text-align: center;
        line-height: 4rem;
        color: white;
        font-weight: 500;
        font-size: 18px;
        @include transition(ease, fast);

        &:hover {
          background-color: rgba(white,.1); }

        &:active {
          background-color: rgba(white,.2); } } }

    [class^=calc-buttons-] {
      width: 16rem;
      flex-wrap: wrap; }

    .calc-buttons-operators-advanced {
      background-color: desaturate(getColor($s-a, -5), 25%); }

    .calc-buttons-operators-basic {
      background-color: desaturate(getColor($s-a, -4), 25%); }

    .calc-container-numbers-controls {

      .calc-buttons-numbers {
        width: 12rem;
        background-color: hsl-map($n-d);
        flex-wrap: wrap-reverse; }

      .calc-buttons-controls {
        width: 4rem;
        background-color: desaturate(getColor($s-c, -3), 25%); } } } }
